.sectionHeader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 65px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  box-shadow: 0px 0px 10px 2px black;
  background-color: var(--main-background-color);
  z-index: 10;
  transition: background-color 250ms linear;
}

.searchInputWrap {
  display: flex;
  justify-content: space-between;
  width: 40%;
  margin-left: 10px;
  background-color: var(--main-background-color);
  border-radius: 5px;
  color: var(--main-text-color);
  transition: background-color 250ms linear;
  &:focus {
    border-color: var(--color-accent);
    outline: none;
  }
}

.searchInput {
  display: block;
  flex-grow: 1;
  align-self: stretch;
  margin-right: 10px;
  padding: 5px;
  font-size: 20px;
  background-color: var(--input-color);
  color: var(--main-text-color);
  border: none;
  box-shadow: 0px 0px 2px 1px var(--main-text-color);
  border-radius: 5px;
  transition: box-shadow 250ms linear;

  &:focus {
    outline: none;
    box-shadow: 0px 0px 2px 1px var(--color-accent);
  }
}

.searchButton {
  margin: 0;
}

.nameAndEmail {
  text-align: end;
}

.mobileMenuBtn {
  width: 45px;
  height: 45px;
  justify-content: center;
  border: none;
  box-shadow: 0px 0px 2px 1px var(--main-text-color);
}
